	module.exports = {"data":{"markdownRemark":{"frontmatter":{"title":"KRASNAYA PESN'YA","type":"lyrics"},"html":"<h1>KRASNAJA PESNJA</h1>\n<section>\n<p>Vspomni<br>\ni spoj mne</p>\n<p>Pust' ryby stanut notami!<br>\nSpoj mne o tom, chto ty zhiv.<br>\nSpoj mne o tom, chto drednouty<br>\ntonut v tebe, kak morzhi.<br>\nTonut i ne vsplyvajut,<br>\nosedajut na dne.<br>\nKity, titaniki, svai<br>\ngrozjat tvoej glubine!</p>\n<p>Vypusti ryb iz gorla — <br>\nnachni svoju krasnuju pesnju!<br>\nPochuvstvuj, kak ty otorvan,<br>\nvymuchen i neizvesten.<br>\nPochuvstvuj, kak blizko radost',<br>\nkak ona pahnet shpalami,<br>\nsnegom elovym i stadom,<br>\nkrov'ju i karavanami.<br>\nPust' pticy stanut notami!<br>\nSpoj mne o tom, kem ty byl,<br>\nkogda byli ljudi smotany<br>\nv uzel i brosheny v il.<br>\nSpoj mne o tom, chto bylo<br>\nposle vojny za vodu.<br>\nSpoj nam, kak cerkov' krestila<br>\nrakety, chasy i zavody.</p>\n<p>Vypusti gorlic iz gorla — <br>\nnachni svoju krasnuju pesnju!<br>\nPochuvstvuj, kak ty otorvan,<br>\nvymuchen i neizvesten.<br>\nPochuvstvuj, kak blizko radost',<br>\nkak ona pahnet ranami,<br>\nkrov'ju i vinogradom,<br>\nsol'ju i karavajami.</p>\n<p>Vysypi zemlju iz gorla — <br>\nnachni svoju krasnuju pesnju!<br>\nDrejfujushhij burevestnik<br>\nzovjot na druguju storonu.</p>\n</section>"},"site":{"siteMetadata":{"languages":{"defaultLangKey":"en","langs":["en","ru"]}}}},"pathContext":{"slug":"/en/lyrics/krasnayaPesnya/"}}

